<template>
    <div class="vertical-gallery" ref="vertical">
        <div ref="action"
             @click="prevFun" class="g-category-nav g-category-up"><span class="material-icons">arrow_drop_up</span>
        </div>
        <div class="vertical-content" :style="{'height': `${height - actionHeight}px`, 'top': `${actionHeight/2}px`}"
             >
            <div class="vertical-container"
                 ref="verticalContainer"
                 :style="{'top': `${contentTop - actionHeight}px`}">
                <slot></slot>        
            </div>
        </div>
         
        <!-- :style="{'height': `${height - actionHeight}px`, 'top': `${actionHeight/2}px`}" -->
    </div>
    <div @click="nextFun" class="g-category-nav g-category-down"><span class="material-icons">arrow_drop_down</span></div>
</template>

<script>
export default {
    data() {
        return {
            height: 'auto',
            actionHeight: 0,
            contentHeight: 0,
            contentTop: 0,
            nextShow: true,
            prevShow: true,
            nextEnd: false,
            windowWidth: window.innerWidth,
        };
    },
    props:{
       itemHeight: {
            type: Number,
            // default: 100
        }
    },
    mounted() {
        window.addEventListener('resize', this.onResize)
        const parentStyle = getComputedStyle(this.$refs.vertical.parentNode);
        this.height = parseInt(parentStyle.height, 10) ;
        const actionStyle = getComputedStyle(this.$refs.action);
        this.actionHeight = parseInt(actionStyle.height, 20) * 2;
        const contentStyle = getComputedStyle(this.$refs.verticalContainer);
        this.contentHeight = parseInt(contentStyle.height, 20) * 2;
        this.nextShow = false;
        this.prevShow = false;
        if (this.contentHeight > this.height - this.actionHeight) {
            this.nextShow = true;
        }
        
    },
    methods: {
        nextFun() {
            const resizeHeight = this.windowWidth > 992 ? 41: 14
            let isItemHeight = this.contentHeight + this.contentTop - this.height + this.actionHeight + resizeHeight;
            if (isItemHeight > 0 && isItemHeight > this.itemHeight) {
                this.contentTop -= this.itemHeight;
                this.prevShow = true;
            }
            else {
                this.contentTop -= isItemHeight;
                this.nextShow = false;
                this.nextEnd = true;

            }
        },
        prevFun() {
            if (this.contentTop < 0 && this.contentTop + this.itemHeight < 0) {
                this.contentTop += this.itemHeight;
                this.nextShow = true;
                
            }
            else {
                this.contentTop = 0;
                this.prevShow = false;
                this.nextEnd = true;

            }
        },
        onResize() {
            this.windowWidth = window.innerWidth
        },
    },
    beforeUnmount() {
        window.removeEventListener('resize', this.onResize)
    },
};
</script>

<style lang='less'>
    .vertical-gallery {
        position: relative;
        height: 100%;
        // overflow: hidden;

        .vertical-content {
            position: absolute;
            min-height: 350px;
            height: 100%;
            left: 0;
            width: 100%;
            // overflow: hidden;
            background-color: #fff;
        }
        .vertical-container {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            transition: all .3s linear;
        }
    }
</style>